@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box ;
}

.App{
    width: 100vw;
    height: 100vh;
    background: #27272A;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

#UtilityBar{
    width: 14%;
    height: 97%;
    background: #016646;
    border-radius: 5px;

}

#Main-Section{
    width: 85%;
    height: 97%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#HeaderInfo{
    width: 97%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

#Title{
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    font-family: Roboto;
    padding-bottom: 1%;
    color: #EDEBD1;
}

#Stats{
    width: 100%;
    height: 78.5%;
    margin-bottom: 1.5%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: row;
}

#TotalEquipment{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;

    background: #016646;
    border-radius: 5px;
   }

#MainDashboard{
    width: 97%;
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;


    background: #016646;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

#LastEdit{
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    color: #EDEBD1;
    font-family: Roboto;
}

#LastEdit img{
    width: 80%;
    height: 45%;
    border-radius: 50%;
}

hr{
    width: 90%;
    height: 1px;
    color: #EDEBD1;
}

#EditButtons{
    width: 100%;
    height: 60%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.UtilityButton{
    width: 85%;
    height: 12%;
    border-radius: 10px;
    border: transparent;
    background: #EDEBD1;
    color: #016646;
    font-size: 1.25rem;
    font-family: "Roboto";
}

.UtilityButton:hover{
    transform: scale(1.075);
    cursor: pointer;

}

.Modal{
    top: 0;
    left: 0;
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1;
}

#Form{
    width: 70%;
    height: 50%;
    background: #EDEBD1;
    border-radius: 5px;
}

#Submit-Area{
    width: 100%;
    height: 15%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: end;
    align-items: center;
    gap: 3rem;
    background: #016646;
    border-radius: 0 0 5px 5px;
    padding-right: 2rem ;
}

#CreateForm{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 70%;
}

#Modal-Title{
    width: 100%;
    height: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #016646;
    font-family: Roboto;
}


.FormButton{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15%;
    height: 70%;
    background: #EDEBD1;
    color: #016646;
    font-family: Roboto;
    font-size: 1.1rem;
    border-radius: 5px;
    border: transparent;
    gap: 2rem;
}

.FormButton:hover{
    transform: scale(1.05);
    cursor: pointer;
}

#CreateInput{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

label{
    display: flex;
    align-items: center;
    width: 50%;
    height: 50%;
    flex-direction: column;
    gap: 1rem;
    color: #016646;
    font-family: Roboto;
    font-size: 1rem;
}

label input,
label select {
    width: 50%;
    height: 20%;
    padding-left: 0.5rem;
}

.FormButton:active{
    transform: translateY(5px);
    transition: 0.4s ease;
}

.Stats-Info{
    width: 33%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.Stats-Data{
    color: #EDEBD1;
    font-size: 7rem;
    font-family: Roboto;
}

.Stats-Desc{
    color: #EDEBD1;
    font-size: 1.1rem;
    font-family: Roboto;

}

.EquipmentList{
    width: 100%;
    height: 90%;
    display: flex;
    justify-content: flex-start;
    overflow-y: auto ;
    align-items: center;
    flex-direction: column;
    gap: 1rem;

}

li{
    list-style: none;
    width: 95%;
    min-height: 10%;
    display: flex;
    border-radius: 2px;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    color: #016646;
    font-family: Roboto;
    font-size: 1rem;
    background: #EDEBD1;
}

li p{
    width: calc(100%/6);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

li:hover{
    transform: scale(1.03);
    transition: 0.35s ease;
}

#EquipmentTitle{
    width: 95%;
    height: 10%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
}

#EquipmentTitle :hover{

    background-color: rgba(39, 39, 42, 0.27);
    text-decoration: underline;
    cursor: pointer;
}

#EquipmentTitle button{
    width: calc(100%/6);
    border: none;
    background: none;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #EDEBD1;
    font-family: Roboto;
    font-size: 1.2rem;
}

#ROMForm{
    width: 100%;
    height: 80%;
}

#Audit-Entries{
    width: 90%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    gap: 1rem;

}

.Audit-Entry{
    width: 100%;
    height: 10%;
    border-radius: 5px;
    background: #016646;
    color: #EDEBD1;
}

.Audit-Entry p{
    width: 97%;
    height: 20%;
    text-wrap: nowrap;
}

@media (max-width: 768px) {
    .App {
        flex-direction: column;
        height: auto;
        min-height: 100vh;
        padding: 10px;
    }

    #UtilityBar {
        width: 100%;
        height: auto;
        min-height: 80px;
        border-radius: 5px;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        padding: 10px;
    }

    #Main-Section {
        width: 100%;
        height: auto;
        padding: 0;
    }

    #HeaderInfo {
        height: auto;
        padding: 10px 0;
    }

    #Title {
        font-size: 1.5rem;
        padding-bottom: 5px;
    }

    #Stats {
        flex-direction: column;
        margin-bottom: 10px;
        gap: 10px;
    }

    .Stats-Info {
        width: 100%;
        padding: 5px 0;
    }

    .Stats-Data {
        font-size: 4rem;
    }

    .Stats-Desc {
        font-size: 1rem;
    }

    #MainDashboard {
        height: auto;
        padding: 15px;
    }

    #LastEdit {
        height: auto;
        padding-bottom: 10px;
    }

    #LastEdit img {
        width: 60%;
        height: auto;
    }

    #EditButtons {
        flex-direction: column;
        gap: 10px;
        padding-top: 10px;
    }

    .UtilityButton {
        width: 90%;
        font-size: 1rem;
        height: auto;
        padding: 12px;
    }

    .Modal {
        padding: 10px;
    }

    #Form {
        width: 95%;
        height: auto;
        max-height: 90vh;
        overflow-y: auto;
    }

    #Submit-Area {
        height: auto;
        padding: 10px 1rem;
        flex-direction: column;
        gap: 10px;
    }

    .FormButton {
        width: 90%;
        height: auto;
        padding: 10px 15px;
        font-size: 1rem;
    }

    #CreateForm {
        height: auto;
        padding: 10px;
    }

    #Modal-Title {
        font-size: 1.2rem;
        height: auto;
        padding: 10px;
    }

    #CreateInput {
        flex-direction: column;
        gap: 10px;
    }

    label {
        width: 95%;
        font-size: 0.9rem;
        align-items: flex-start;
    }

    label input,
    label select {
        width: 100%;
        height: 35px;
    }

    .EquipmentList {
        padding-left: 5px;
        padding-right: 5px;
    }

    li {
        flex-direction: column;
        align-items: flex-start;
        gap: 5px;
        font-size: 0.9rem;
        padding: 10px;
    }

    li p {
        width: 100%;
        justify-content: flex-start;
        text-align: left;
    }

    #EquipmentTitle {
        flex-direction: column;
        gap: 5px;
        padding: 5px 0;
    }

    #EquipmentTitle button {
        width: 100%;
        font-size: 1rem;
        text-align: center;
    }

    .Audit-Entry {
        font-size: 0.85rem;
        padding: 8px;
        height: auto;
    }

    .Audit-Entry p {
        font-size: 0.85rem;
        text-wrap: wrap;
        width: 100%;
        height: auto;
    }
}

